.works {
	width: 100%;
	background-color: #fff;
	.search-box-all-works {
		position: sticky;
		top: 3rem;
		z-index: 2;
		background-color: $background-primary;
		border-top: 2px solid white;
	}
}
.action-work {
	display: flex;
	gap: 10px;
	justify-content: space-between;
	align-items: center;
	margin: 1rem 0px;
	/* background: gray; */
	padding: 10px;
	border-radius: 4px;
	box-shadow: 1px 1px 10px rgba(214, 213, 213, 0.7);
	// justify-content: space-around;
}

.works-grid {
	// display: grid;
	// grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
	// grid-template-columns: repeat(4, 1fr);
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	justify-content: center;
	align-items: center;
	justify-items: center;
	// justify-items: center;

	padding-left: 20px;
	// background: rgb(241, 240, 240);
	grid-gap: 0.5rem;
	// grid-gap: 1.5rem;
	// padding: 10px;
	.no-result {
		display: flex;
		justify-content: center;
		gap: 1rem;
		align-items: center;
		flex-direction: column;
		padding: 10px;
		border-radius: 4px;
		box-shadow: 1px 1px 10px rgba(214, 213, 213, 0.7);
		height: auto;
		i {
			font-size: 70px;
			color: red;
			width: 100px;
			height: 100px;
			background: white;
			border: 1px solid #374151;
			text-align: center;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			box-shadow: 1px 1px 37px #a9a8a8;
		}
	}
	.no-works {
		text-align: center;
		font-size: 1rem;
		letter-spacing: 2px;
		color: $gray;
		text-transform: uppercase;
		span {
			font-size: 1.5rem;
			color: #ff3d1b;
		}
	}
}
