.work {
	.img-work {
		img {
			border-bottom: 0.5px solid #f7e9e9;
		}
	}
	.borrar-trabajo {
		display: none;
		position: absolute;
		right: 10px;
		top: 5px;
		background: red;
		width: 40px;
		height: 40px;
		text-align: center;
		// display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50px;
		color: #f9fafb;
		font-size: 26px;
		box-shadow: 1px 2px 5px #1f2937, -1px -2px 5px #1f2937;
		z-index: 1;
		cursor: pointer;
		transition: 500ms;
	}
	overflow: hidden;

	display: flex;
	justify-content: space-between;
	flex-direction: column;
	// align-items: center;
	min-width: 250px;
	position: relative;
	background-color: #fff;
	border-radius: 10px;
	color: inherit;
	transition: 500ms ease-in-out;
	// box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.26);
	box-shadow: $shadow;
	border-radius: 3px;
	max-width: 270px;
	// min-width: 250px;

	// padding: 10px;
	&:hover {
		img {
			transform: scale(1.04);
		}
		box-shadow: 2px 2px 18px rgba(148, 146, 146, 0.9);
		.borrar-trabajo {
			display: flex;
		}
		a {
			text-decoration: underline;
		}
	}
	.precio {
		padding: 10px;
		text-align: center;
		color: white;
		width: 50px;
		height: 50px;
		background: $gray;
		position: absolute;
		border-radius: 50%;
		right: -10px;
		top: -10px;
		box-shadow: 1px 2px 9px white;
		span {
			text-align: center;
			font-size: 11px;
		}
	}
	i {
		font-size: 30px;
		// color: $color-primary;
		text-align: center;
		padding: 10px 0px;
	}
	.work-lock {
		font-size: 15px;
		padding: 0;
		padding-left: 10px;
		align-items: center;
		border-radius: 50%;
		&:hover {
			opacity: 0.7;
			transform: scale(1.21);
		}
	}
	.work-body {
		padding: 10px;
	}
	.work-estado {
		text-align: center;
		background: $gray;
		padding: 5px;
		span {
			font-size: 17px;
			color: white;
		}
	}
	.observaciones {
		height: 100px;
	}
	a {
		// text-decoration: underline;
		// text-align: center;
		// color: white;
		// text-transform: uppercase;
		// font-weight: bold;
		// width: 100%;
		// // background-color: $color-primary;
		// &:hover {
		// 	background-color: darken($color: $color-primary, $amount: 10);
		// }
		// padding: 10px 0px;
	}
}
.work-entregados {
	display: none;
	&.active {
		display: table;
	}
}
// work add

.work-add {
