	justify-content: center;
	flex-direction: column;
	align-items: center;
	background-color: #fff;
	width: 100%;
	h3 {
		color: $gray;
		font-size: 40px;
		font-weight: bold;
		letter-spacing: 1px;
		text-align: center;
		// background-color: aqua;
	}
	form {
		// background-color: #fff;

		// justify-content: space-around;
		// align-items: center;
		// flex-direction: column;

		// width: auto;
		padding: 10px;
		// box-shadow: 1px 1px 10px rgba(214, 213, 213, 0.7);
		box-shadow: rgba(214, 213, 213, 0.7) 8px 8px 10px,
