label {
	display: block;
	color: $gray;
	font-size: 20px;
	text-transform: capitalize;
}
input {
	padding: 10px 30px;
}
textarea {
	width: 100%;
	height: 80px;
	resize: none;
	margin: auto;
	font-family: 'Rubik';
}
button {
	background-color: $color-primary;
	display: block;
	width: 100%;
	&:hover {
		background-color: darken($color: $color-primary, $amount: 10);
	}
}
.grid-content {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 0.5rem;
}

// get One Work
.one-work {
	padding: 10px;
	border-radius: 4px;
	&-grid {
		display: grid;
		justify-content: center;
		align-items: center;
		grid-template-columns: 30% 70%;
		grid-gap: 0.5rem;
		// background-color: red;
		padding: 10px;
		box-shadow: 1px 1px 10px rgba(214, 213, 213, 0.7);
		i {
			font-size: 150px;
		}
		// a {
		//   background-color: $color-primary;
		//   padding: 10px;
		//   border-radius: 50px;
		// }
	}
	.overlay {
		visibility: hidden;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgba(0, 0, 0, 0.4);
		.close-modal {
			position: absolute;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
			background-color: rgba(0, 0, 0, 0.3);
		}
		.modal-edit-work {
			position: relative;
			display: flex;
			justify-content: space-between;
			flex-direction: column;
			background-color: #fff;
			padding: 10px;
			width: 600px;
			max-width: 90%;
			margin: auto;
			box-shadow: 1px 1px 10px rgba(214, 213, 213, 0.7);
			border-radius: 3px;
			z-index: 15;
			.close {
				position: absolute;
				top: 10px;
				right: 10px;
				font-size: 20px;
				color: white;
				background-color: $color-primary;
				border-radius: 50%;
				padding: 10px;
				width: 20px;
				height: 20px;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
	}
	.overlay:target {
		visibility: visible;
	}
}
.one-work-body .work-data,
.one-work-body .price-data,
.one-work-body .client-data,
.one-work-body .trouble-data {
	height: auto;
	width: auto;
	min-width: 200px;
}
.obs {
	height: auto;
	width: 250px;
	position: absolute;
	background-color: #374151;
	padding: 10px;
	padding-bottom: 15px;
	// right: 3rem;
	z-index: 2;
	// top: 24rem;
	&::before {
		content: '';
		width: 20px;
		height: 20px;
		transform: rotate(45deg);
		background-color: #374151;
		position: absolute;
		top: -10px;
		left: 50% - 2%;
		z-index: 1;
	}
}

.labelPassword {
	flex-basis: 70%;
}
.checkPassword {
	flex-basis: 30%;
}
@media (max-width: 1600px) {
	.grid-content {
		grid-template-columns: 1fr 1fr;
	}
}

@media (max-width: 750px) {
	.work-add {
		h3 {
			font-size: 30px;
			color: $color-primary;
		}
	}
	.grid-content {
		grid-template-columns: 1fr;
	}
}
@media (max-width: 1100px) {
	.works-grid {
		// grid-template-columns: repeat(3, 1fr);
		grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	}
}

@media (max-width: 900px) {
	.works-grid {
		grid-template-columns: repeat(2, 1fr);
	}
}
@media (max-width: 500px) {
	.works-grid {
		grid-template-columns: 1fr;
	}
	.work {
		width: 95%;
		margin: auto;
		max-width: none;
	}
	.content-principal-margin {
		// padding: 4px;
	}
	.one-work-grid {
		grid-template-columns: 1fr;
		grid-gap: 1rem;
	}
	.one-work-body {
		grid-template-columns: 1fr;
		grid-template-rows: none;
	}
	.one-user {
		flex-direction: column;
		gap: 1rem;
		.user-header-action {
			width: 100%;
		}
	}
	.workStateWithoutChange .swiper-wrapper .swiper-slide:first-child {
		margin: 0 !important;
	}
	.swiper-slide {
		width: 100% !important;
		padding: 0 !important;
		margin: 0 !important;
		// display: flex;
		// justify-content: center;
		// align-items: center;
	}
}
.oneWorkCarrousel {
	width: 250px;
	min-height: auto;
	.swiper-wrapper {
		.swiper-slide {
			width: 100%;
			padding: 0;
			margin: 0;
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				width: 100%;
			}
		}
	}
}

.swiper-button-next.swiper-button-disabled {
	display: none;
}

.add-work-user:hover {
	i {
		// font-size: 120px;
		transform: rotate(360deg);
	}
}
.add-work-user {
	i {
		transition: 1s ease-in-out;
		font-size: 100px;
	}
}
.history {
	border-bottom: 0.4px solid rgb(196, 196, 196);
