}
.modal-borrar-work {
	position: absolute;
	background: #02091c42;
	z-index: 10;
	display: none;
	justify-content: center;
	height: 100%;
	align-items: center;
	width: 100%;
	.modal-box {
		display: flex;
		flex-direction: column;
		background: white;
		box-shadow: 1px 2px 5px white;
		gap: 1rem;
		width: 90%;
		.header {
			padding: 0.2rem;
			background: red;
			color: white;
			text-align: center;
		}
		.btns {
			display: flex;
			gap: 0.6rem;
			justify-content: center;
			padding: 0.3rem;
			align-items: center;
			.borrar {
				background-color: red;
			}
			.cancel {
				background-color: #1f2937;
			}
		}
	}
}

.modal-edit-work.active {
	display: flex;
}

.update-state {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 0.5rem;
	background: #f9fafb;
	box-shadow: 1px 2px 5px #d1d5db;
	margin: 1rem 0;
	padding: 0.3rem;
	overflow: auto;
	button {
		border-radius: 3px;
	}
}
